import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ResolucionDocenteService } from '../../services/resoluciondocente.service';
import { ResolucionModel } from '../../models/gestion-academica/resolucion.model';
import { DocenteModel } from '../../models/gestion-academica/docente.model';

@Component({
  selector: 'app-modal-detalle-docente',
  templateUrl: './modal-detalle-docente.component.html',
  styles: [
  ]
})
export class ModalDetalleDocenteComponent implements OnInit {

  @ViewChild('modalDetalleDocenteBanner', { static: false })
  modalDetalleDocente: ElementRef;

  public resolucion: ResolucionModel = null;
  public pdf: string = ""
  public listaDocente: DocenteModel[] = []
  public cargado: Boolean = false
  public verPdf: Boolean = false;
  public textoBoton: string = "Ver Pdf";
  constructor(
    private modalService: NgbModal,
    private resolucionDocenteService: ResolucionDocenteService

  ) { }

  ngOnInit(): void {
  }
  openModal(resolucion: ResolucionModel) {
    this.resolucion = resolucion;
    this.buscarDocentes();
    this.modalService.open(this.modalDetalleDocente, { size: 'xl', ariaLabelledBy: 'modal-basic-title', centered: true }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false))
    this.pdf = resolucion.archivoPdf

  }

  buscarDocentes() {
    this.cargado = false;
    this.listaDocente = []
    const numeroResolucion = this.resolucion.numeroResolucion.replace("/", "_");
    this.resolucionDocenteService.getDocenteByNumeroResolucion(numeroResolucion).subscribe(
      (value) => {
        this.listaDocente = value
        this.cargado = true;
      });
  }

  cerrarModal(dimissAll = true) {
    this.resolucion = null;
    this.pdf = "";
    if (dimissAll) this.modalService.dismissAll();
    this.textoBoton = "Ver pdf"
    this.verPdf = false
  }

  cambioModal() {
    this.verPdf = !this.verPdf
    if (this.verPdf) {
      this.textoBoton = "Ver Docente(s)"
    } else {
      this.textoBoton = "Ver Pdf"
    }
  }

}
