import { FacultadModel } from "./facultad.model";
import { PeriodoModel } from './periodo.model';

export class ResolucionModel {
  numeroResolucion = "";
  archivoPdf = "";
  fechaCreacion = null;
  fechaResolucion = null;
  facultadId = null;
  usuarioCreacion = "";
  periodo: PeriodoModel = null;

  constructor(fields?: {
    numeroResolucion?: String,
    archivoPdf?: String,
    fechaCreacion?: Date,
    fechaResolucion?: Date,
    facultadId?: FacultadModel,
    usuarioCreacion?: String,
    periodo?: PeriodoModel



  }) {
    if (fields) { Object.assign(this, fields); }
  }
}