import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { UtilAlert } from '../../commons/util/util.alert';
import { AppState } from '../../store/app.reducer';
import { Store } from '@ngrx/store';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { AppService } from '../../app.service';
import { FormGroup, FormControl } from '@angular/forms';
import { SearchPagination } from '../../commons/interfaces/search.pagination';
import { Subscription } from 'rxjs';
import { ResolucionModel } from 'src/app/commons/models/gestion-academica/resolucion.model';
import { paginacionResolucion } from '../../store/actions/resolucion.actions';
import { ModalDetalleDocenteComponent } from '../../commons/components/modal-detalle-docente/modal-detalle-docente.component';

@Component({
  templateUrl: './admin-resoluciones.component.html',
  styles: [
  ]
})
export class AdminResolucionesComponent implements OnInit, OnDestroy {
  public formTopBar: FormGroup;
  private _subscriptionResolucion: Subscription;
  public listaResoluciones: ResolucionModel[] = [];
  //------------------------------
  public loaded: boolean = false;
  public page: number = 1;
  public pageSize: number = 10;
  public buscador = "";
  public totalElements = 0;
  public searchPagination: SearchPagination<string>
  private timer = null;

  @ViewChild(ModalDetalleDocenteComponent, { static: false })
  public modalDetalleDocente: ModalDetalleDocenteComponent;

  constructor(
    private router: Router,
    private appService: AppService,
    public toastr: ToastrService,
    public store: Store<AppState>,
    public alertService: UtilAlert,
  ) {
    this.appService.pageTitle = "Administrador de Resoluciones"
  }

  ngOnInit(): void {
    this._resolucionState();
    this.crearTopBar();
    this.listarResolucion();
  }
  ngOnDestroy(): void {
    this._subscriptionResolucion.unsubscribe();
    this.loaded = false
  }

  crearTopBar() {
    this.formTopBar = new FormGroup({
      tipo: new FormControl(""),
      registros: new FormControl("")
    });

    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  buscar(texto: string) {
    this.buscador = texto;
    /**
 .    * Se usa para evitar una petición cada vez que se presione una tecla y se haga solo cuando se termine de escribir.
     **/
    if (this.timer != null) clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      this.listarResolucion();
      this.timer == null;
    }, 1000);
  }

  listarResolucion() {
    this.listaResoluciones = [];
    this.loaded = false;
    const searchPagination: SearchPagination<string> = {
      page: this.page,
      records: this.pageSize,
      seek: this.buscador ? this.buscador : "",
      order: "resolucion.fechaCreacion",
      direction: "desc"
    };
    this.store.dispatch(paginacionResolucion({ searchPagination })
    );
    return;
  }

  private _resolucionState() {
    this._subscriptionResolucion = this.store.select("resolucion").subscribe(
      (state) => {
        if (state.paginacionResolucion != null) {
          this.page = state.searchPagination.page;
          this.pageSize = state.searchPagination.records;
        }
        if (state.loaded && state.paginacionResolucion) {
          this.loaded = state.loaded
          this.listaResoluciones = state.paginacionResolucion.content;
          this.totalElements = state.paginacionResolucion.totalElements;
        }
      }
    );
  }
  showPdf(resolucion: ResolucionModel) {
    const linkSource = 'data:application/pdf;base64,' + resolucion.archivoPdf;
    const downloadLink = document.createElement("a");
    const fileName = "Resolución " + resolucion.numeroResolucion;

    downloadLink.href = linkSource;
    downloadLink.download = fileName;
    downloadLink.click();
  }

  abrirModal(resolucion: ResolucionModel) {
    this.modalDetalleDocente.openModal(resolucion)
  }

}
