import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { SubjerarquiaModel } from '../models/gestion-academica/subjerarquia.model';
@Injectable({
    providedIn: 'root'
})
export class SubjerarquiaService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllSubjerarquia(): Observable<SubjerarquiaModel[]> {
        return this.http.get<SubjerarquiaModel[]>(`${environment.backend.gestion_academica}api/subjerarquia/v1/get-all`);
    }

}