import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { EscuelaModel } from '../models/gestion-academica/escuela.model';
import { environment } from 'src/environments/environment';
@Injectable({
    providedIn: 'root'
})
export class EscuelaService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllEscuela(): Observable<Array<EscuelaModel>> {
        return this.http.get<Array<EscuelaModel>>(`${environment.backend.gestion_academica}api/escuela/v1/get-all`);
    }

}