import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { SearchPagination } from '../../interfaces/search.pagination';
import { DocenteModel } from '../../models/gestion-academica/docente.model';
import { ToastrService } from 'ngx-toastr';
import { UtilValidation } from '../../util/util.validation';
import { AdminResolucionesComponent } from '../../../paginas/admin-resoluciones/admin-resoluciones.component';
import { AdminResolucionesNewComponent } from '../../../paginas/admin-resoluciones/admin-resoluciones-new/admin-resoluciones-new.component';
import { JerarquiaModel } from '../../models/gestion-academica/jerarquia.model';
import { SubjerarquiaModel } from '../../models/gestion-academica/subjerarquia.model';
import { FacultadModel } from '../../models/gestion-academica/facultad.model';
import { EscuelaModel } from '../../models/gestion-academica/escuela.model';
import { EscuelaService } from '../../services/escuela.service';
import { DocenteService } from '../../services/docente.service';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { JerarquiaService } from '../../services/jerarquia.service';
import { SubjerarquiaService } from '../../services/subjerarquia.service';

@Component({
  selector: 'app-modal-docente',
  templateUrl: './modal-docente.component.html',
  styles: [
  ]
})
export class ModalDocenteComponent implements OnInit {
  @ViewChild('modalDocente', { static: false })
  modalDocente: ElementRef;
  @BlockUI() blockUI: NgBlockUI;


  public formDocente: FormGroup;
  public docente: DocenteModel;
  public listaDocentes: DocenteModel[] = []
  public listaFacultad: FacultadModel[] = []
  public listaEscuela: EscuelaModel[] = []
  public docenteBanner: DocenteModel;
  public listaJerarquia: JerarquiaModel[] = [];
  public listaSubjerarquia: SubjerarquiaModel[] = [];

  //Paginación
  public page: number = 0;
  public pageSize: number = 0;
  public buscador = "";
  public searchPagination: SearchPagination<string>;
  public direccionEstado: string = "asc";
  public order: string;


  constructor(
    private modalService: NgbModal,
    private toastr: ToastrService,
    private adminResoluciones: AdminResolucionesNewComponent,
    private utilValidation: UtilValidation,
    private escuelaService: EscuelaService,
    private docenteService: DocenteService,
    private jerarquiaService: JerarquiaService,
    private subjerarquiaService: SubjerarquiaService,

  ) { }

  ngOnInit(): void {
    this.newFormDocente();
    this.getJerarquiaSubjerarquia();
  }

  openModal(listaDocentes: DocenteModel[]) {
    this.listaDocentes = listaDocentes;
    this.modalService.open(this.modalDocente, { size: 'md', ariaLabelledBy: 'modal-basic-title', centered: true }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false))
  }
  insertar() {
    this.activarCampos()
    this.docente = new DocenteModel();
    this.formatoRut();
    if (this.listaDocentes.length > 0) {
      let index: number = 0;
      do {
        if (this.formDocente.get("rutDocente").value == this.listaDocentes[index].rut) {
          this.desactivarCampos()
          return this.toastr.info("El docente ingresado ya se encuentra agregado")
        }
        index++
      } while (index < this.listaDocentes.length);

    }
    UtilValidation.setFormForValidate(this.formDocente);
    if (this.formDocente.valid) {
      let jerarquiaModel: JerarquiaModel = new JerarquiaModel();
      let subjerarquiaModel: SubjerarquiaModel = new SubjerarquiaModel();
      this.docente.rut = this.formDocente.get("rutDocente").value;
      this.docente.nombre = this.formDocente.get("nombreDocente").value;
      this.docente.apellido = this.formDocente.get("apellidoDocente").value;
      this.docente.jerarquia = this.obtenerJerarquia()
      this.docente.subjerarquia = this.obtenerSubjerarquia();
      this.docente.escuela = this.docenteBanner.escuela;
      this.docente.facultad = this.docenteBanner.facultad;
      this.docente.fechaActa = this.formDocente.get("fechaActa").value;
      this.adminResoluciones.addDocente(this.docente);
      this.desactivarCampos();
      this.cerrarModal()

    } else {
      if (this.formDocente.get('nombreDocente').value == "" || this.formDocente.get('apellidoDocente').value == "" || this.formDocente.get('escuela').value == "" || this.formDocente.get('facultad').value == "") return this.toastr.warning("Ingrese un académico válido", "ATENCIÓN:")
      this.toastr.warning("Ingrese todos los campos obligatorios antes de insertar", "ATENCIÓN:")
      this.desactivarCampos()
    }

  }

  formatoRut() {
    var value: string = this.formDocente.get("rutDocente").value;
    var rut = this.rutClean(value);
    if (rut.length <= 1) {
      return;
    }
    var result = rut.slice(-4, -1) + "-" + rut.substr(rut.length - 1);
    for (var i = 4; i < rut.length; i += 3) {
      result = rut.slice(-3 - i, -i) + result;
    }
    return this.formDocente.get("rutDocente").setValue(result);
  }

  rutClean(value) {
    return typeof value === 'string' ? value.replace(/[^0-9kK]+/g, '').toUpperCase() : '';
  }
  //TODO:TERMINAR EL FORM
  newFormDocente() {
    this.formDocente = new FormGroup({
      rutDocente: new FormControl("", [Validators.required, this.utilValidation.rutValido]),
      nombreDocente: new FormControl("", [Validators.required]),
      apellidoDocente: new FormControl("", [Validators.required]),
      jerarquia: new FormControl("", [Validators.required]),
      subjerarquia: new FormControl("", [Validators.required]),
      escuela: new FormControl("", [Validators.required]),
      facultad: new FormControl("", [Validators.required]),
      fechaActa: new FormControl("", [Validators.required])
    });

    this.formDocente.get("nombreDocente").disable();
    this.formDocente.get("apellidoDocente").disable();
    this.formDocente.get("escuela").disable();
    this.formDocente.get("facultad").disable();
  }
  reiniciarModal() {
    this.formDocente.get("rutDocente").setValue("");
    this.formDocente.get("nombreDocente").setValue("");
    this.formDocente.get("apellidoDocente").setValue("");
    this.formDocente.get("jerarquia").setValue("");
    this.formDocente.get("subjerarquia").setValue("");
    this.formDocente.get("escuela").setValue("");
    this.formDocente.get("facultad").setValue("");
    this.formDocente.get("fechaActa").setValue("");
    this.formDocente.markAsUntouched();
  }
  cerrarModal(dimissAll = true) {
    if (!this.formDocente.get("rutDocente").valid) {
      return;
    }
    if (dimissAll) this.modalService.dismissAll();
    this.reiniciarModal();
  }

  getDatosDocente() {
    if (this.formDocente.get("rutDocente").value == "") {
      this.reiniciarModal()
      return;
    }
    this.formatoRut();
    if (this.buscarDocenteRepetido()) {
      this.toastr.warning("El docente ya se ha insertado", "ATENCION:");
      return;
    }
    this.blockUI.start("Obteniendo datos del docente...")
    let rut: string = this.formDocente.get("rutDocente").value;
    this.docenteService.getDocenteBannerById(rut).subscribe((value) => {
      if (value == null) {
        this.blockUI.stop();
        return this.toastr.warning("El docente " + rut + " no se ha encontrado");
      }
      this.docenteBanner = value;
      this.formDocente.get("nombreDocente").setValue(value.nombre)
      this.formDocente.get("apellidoDocente").setValue(value.apellido)
      this.formDocente.get("escuela").setValue(value.escuela.nombre)
      this.formDocente.get("facultad").setValue(value.facultad.nombre)
      this.blockUI.stop();

    })
  }

  activarCampos() {
    this.formDocente.get("nombreDocente").enable();
    this.formDocente.get("apellidoDocente").enable();
    this.formDocente.get("escuela").enable();
    this.formDocente.get("facultad").enable();
  }

  desactivarCampos() {
    this.formDocente.get("nombreDocente").disable();
    this.formDocente.get("apellidoDocente").disable();
    this.formDocente.get("escuela").disable();
    this.formDocente.get("facultad").disable();
  }

  getJerarquiaSubjerarquia() {
    this.blockUI.start("Obteniendo Jerarquias y Subjerarquias...")
    this.jerarquiaService.getAllJerarquia().subscribe((value) => {
      this.listaJerarquia = value
      if (this.listaJerarquia.length > 0 && this.listaSubjerarquia.length > 0) {
        this.blockUI.stop()
      }
    })

    this.subjerarquiaService.getAllSubjerarquia().subscribe((value) => {
      this.listaSubjerarquia = value
      if (this.listaJerarquia.length > 0 && this.listaSubjerarquia.length > 0) {
        this.blockUI.stop()
      }
    })
  }

  obtenerJerarquia(): JerarquiaModel {
    let contador: number = 0
    do {
      if (this.formDocente.get("jerarquia").value == this.listaJerarquia[contador].codigo) {
        return this.listaJerarquia[contador];
      }
      contador++
    } while (contador < this.listaJerarquia.length)

    return new JerarquiaModel()
  }

  obtenerSubjerarquia(): SubjerarquiaModel {
    let contador: number = 0
    do {
      if (this.formDocente.get("subjerarquia").value == this.listaSubjerarquia[contador].codigo) {
        return this.listaSubjerarquia[contador];
      }
      contador++
    } while (contador < this.listaSubjerarquia.length)
    return new SubjerarquiaModel()
  }

  buscarDocenteRepetido(): boolean {
    if (this.listaDocentes.length == 0) return;
    var contador = 0
    do {
      console.log(this.formDocente.get("rutDocente").value + ' + ' + this.listaDocentes[contador].rut)
      if (this.formDocente.get("rutDocente").value == this.listaDocentes[contador].rut) {
        console.log('true')
        return true;
      }
      contador++
    } while (contador < this.listaDocentes.length)
    return false;
  }
}
