import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { AppService } from '../../../app.service';
import { ToastrService } from 'ngx-toastr';
import { AppState } from '../../../store/app.reducer';
import { Store } from '@ngrx/store';
import { UtilAlert } from '../../../commons/util/util.alert';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ModalDocenteComponent } from '../../../commons/components/modal-docente/modal-docente.component';
import Swal from 'sweetalert2';
import { Subscription } from 'rxjs';
import { destroyResolucionDocente, guardarResolucionDocente } from '../../../store/actions/resolucion-docente.actions';
import { NgBlockUI, BlockUI } from 'ng-block-ui';
import { ResolucionDocenteModel } from 'src/app/commons/models/gestion-academica/resolucion-docente.model';
import { ResolucionModel } from 'src/app/commons/models/gestion-academica/resolucion.model';
import { UtilValidation } from '../../../commons/util/util.validation';
import { FacultadModel } from 'src/app/commons/models/gestion-academica/facultad.model';
import { FacultadService } from '../../../commons/services/facultad.service';
import { PeriodoModel } from 'src/app/commons/models/gestion-academica/periodo.model';
import { PeriodoService } from 'src/app/commons/services/periodo.service';
import { ResolucionService } from 'src/app/commons/services/resolucion.service';
import { IndicadorPublicacionesService } from 'src/app/commons/services/indicador-publicaciones.service';
import { DocenteModel } from 'src/app/commons/models/gestion-academica/docente.model';

@Component({
  selector: 'app-admin-resoluciones-new',
  templateUrl: './admin-resoluciones-new.component.html',
  styles: [
  ]
})
export class AdminResolucionesNewComponent implements OnInit, OnDestroy {
  public listaFacultades: FacultadModel[] = [];
  public formResolucion: FormGroup;
  public listaDocentes: DocenteModel[] = [];
  public files: File[] = [];
  public archivoPdf: string;
  public resolucionesDocentes: ResolucionDocenteModel[] = [];
  private _subscriptionResolucionDocente: Subscription;
  public guardando: boolean = false;
  public listaPeriodos: PeriodoModel[] = []
  @BlockUI() blockUI: NgBlockUI;
  @ViewChild(ModalDocenteComponent, { static: false })
  public modalDocente: ModalDocenteComponent;
  public listaResoluciones: string[] = []

  constructor(
    private router: Router,
    private appService: AppService,
    public toastr: ToastrService,
    public store: Store<AppState>,
    public alertService: UtilAlert,
    private facultadesService: FacultadService,
    private periodoService: PeriodoService,
    private resolucionService: ResolucionService,
    private indicadorPublicacionesService: IndicadorPublicacionesService,
  ) {
    this.appService.pageTitle = "Nueva Resolución";
  }

  ngOnInit(): void {
    this.crearResolucion();
    this.resolucionDocenteState();
    this.obtenerResoluciones();
    this.obtenerFacultades();
    this.obtenerPeriodos();

  }

  ngOnDestroy(): void {
    this._subscriptionResolucionDocente.unsubscribe();
  }

  private resolucionDocenteState() {
    this._subscriptionResolucionDocente = this.store.select("resolucionDocente").subscribe((state) => {
      if (state.loadedGuardado && state.resolucionDocente) {
        this.alertService.successSwal(
          `Se ha registrado correctamente la resolución`
        );
        this.guardando = false;
        this.router.navigate(["/admin-resoluciones"])
        this.store.dispatch(destroyResolucionDocente())
        this.blockUI.stop();
      }
    });
  }
  crearResolucion() {
    this.formResolucion = new FormGroup({
      facultad: new FormControl("", [Validators.required]),
      resolucion: new FormControl("", [Validators.required]),
      fechaResolucion: new FormControl(null, [Validators.required]),
      periodoResolucion: new FormControl(null, [Validators.required])
    });
  }
  onSelect(event) {
    this.archivoPdf = null;
    if (this.files.length > 0) {
      this.files.pop();
    }
    this.files.push(...event.addedFiles);
    const reader = new FileReader();
    reader.readAsDataURL(this.files[0]);
    reader.onload = () => {
      this.archivoPdf = reader.result.toString().replace("data:application/pdf;base64,", "")
    };
  }

  onRemove(event) {
    this.files.splice(this.files.indexOf(event), 1);
  }

  abrirModal() {
    if (this.files.length == 0) {
      return Swal.fire('Acción no Permitida', 'Primero debes añadir el documento PDF', 'warning')
    }
    this.modalDocente.openModal(this.listaDocentes)
  }

  addDocente(docente: DocenteModel) {
    this.listaDocentes.push(docente)
  }

  eliminarDocente(self: DocenteModel) {
    this.listaDocentes.splice(this.listaDocentes.indexOf(self), 1)
  }

  obtenerResolucionDocente() {
    this.resolucionesDocentes = []
    //Se genera la nueva resolucion y se rellena con los datos ingresados.
    let resolucion: ResolucionModel = new ResolucionModel();
    let facultad: FacultadModel = new FacultadModel();
    let periodo: PeriodoModel = new PeriodoModel()
    resolucion.archivoPdf = this.archivoPdf;
    facultad.codigo = this.formResolucion.get("facultad").value;
    resolucion.facultadId = facultad;
    resolucion.fechaCreacion = new Date();
    resolucion.fechaResolucion = this.formResolucion.get("fechaResolucion").value;
    periodo.codigo = this.formResolucion.get("periodoResolucion").value;
    resolucion.numeroResolucion = this.formResolucion.get("resolucion").value;
    resolucion.periodo = periodo


    // Se genera el listado de resoluciones docentes y se prepara para mandarlas 
    let contador: number = 0
    do {
      let docenteResolucion: ResolucionDocenteModel = new ResolucionDocenteModel;
      docenteResolucion.resolucion = resolucion;
      docenteResolucion.docente = this.listaDocentes[contador];
      this.resolucionesDocentes.push(docenteResolucion);
      contador++
    } while (contador < this.listaDocentes.length);


  }

  obtenerFacultades() {
    this.blockUI.start("Obteniendo Facultades");
    this.facultadesService.getAllFacultades().subscribe((value) => {
      this.listaFacultades = value;
      this.blockUI.stop();
    }, (error) => {
      this.blockUI.stop();
    });
  }
  obtenerPeriodos() {
    this.blockUI.start("Obteniendo Periodos");
    this.periodoService.getAllPeriodos().subscribe((value) => {
      this.listaPeriodos = value;
      this.blockUI.stop();
    }, (error) => {
      this.blockUI.stop();
    });
  }

  guardar() {
    if (this.listaDocentes.length == 0 || this.files.length == 0) {
      return Swal.fire('Acción no Permitida', 'Asegurate de haber añadido el archivo PDF y de haber añadido a lo menos un docente.', 'warning');
    }
    UtilValidation.setFormForValidate(this.formResolucion);
    if (this.formResolucion.invalid) {
      this.toastr.info("Ingrese todos los datos antes de guardar");
      return;
    }
    var resolucion: string = this.formResolucion.get("resolucion").value;
    console.table(this.listaResoluciones)
    console.log(resolucion)
    console.log(this.listaResoluciones.includes(resolucion))
    if (this.listaResoluciones.includes(resolucion)) {
      this.toastr.warning("El número de resolución ingresado ya se encuentra registrado")
      return;
    }

    var texto = this.generarTexto();
    Swal.fire({
      title: '¿Estás Seguro que los datos son correctos?',
      html: texto,
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#198754',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true,
    }).then((result) => {
      if (result.isConfirmed) {
        this.obtenerResolucionDocente();
        this.guardando = true;
        this.store.dispatch(guardarResolucionDocente({ resolucionDocente: this.resolucionesDocentes }))
      }
    })
  }

  generarTexto(): string {

    var texto = "";
    texto += "Se va a añadir la siguiente información:<br><strong>Número de resolucion:</strong> " + this.formResolucion.get('resolucion').value + "<br><strong>Periodo</strong>: " + this.formResolucion.get("periodoResolucion").value + "<br><br><strong>Con los siguientes docentes:</strong><br><br>"
    var contador = 0
    do {
      texto += "<strong>Nombre: </strong>" + this.listaDocentes[contador].nombre + " " + this.listaDocentes[contador].apellido + "<br><strong>Jerarquía: </strong>" + this.listaDocentes[contador].jerarquia.nombre + "<br><strong>Subjerarquía: </strong>" + this.listaDocentes[contador].subjerarquia.nombre + "<br>";
      if (!(contador + 1 == this.listaDocentes.length)) {
        texto += "<br>"
        //SI NO HAY MAS DATOS SE AGREGA UN ESPACIO MÁS PARA QUE QUEDEN MÁS SEPARADOS
      }
      contador++
    } while (contador < this.listaDocentes.length)
    return texto
  }

  obtenerResoluciones() {
    this.blockUI.start("Obteniendo lista de resoluciones")
    this.resolucionService.getAll().subscribe((value) => {
      this.listaResoluciones = value;
      this.blockUI.stop();
    }, (Error) => {
      this.toastr.error("Error al intentar obtener la lsita de resoluciones")
      this.blockUI.stop();
    }, () => {
      this.blockUI.stop()
    })
  }
}

